export const ShareBlock = ({ isHeritage, impots }) => {
  const link = isHeritage ? "/heritage" : "/";
  const encodedLink = encodeURIComponent(link);
  const gain = isHeritage ? impots.current - impots.next : impots.gain;
  const gainString = `${numeral(gain).format("0,0")} `;
  const winner = isHeritage ? "mon hritage gagnera" : "je gagnerai";

  const URL = encodeURIComponent("https://impots.melenchon2022.fr");

  const urlTwitter = `https://twitter.com/intent/tweet?text=${encodeURIComponent(
    !gain || gain < 0
      ? "Simulez votre imposition avec Mlenchon en 10 secondes. Testez vous aussi !"
      : `Avec la #RvolutionFiscale de Mlenchon, ${winner} ${gainString}. Testez vous aussi !`
  )}&url=https://impots.melenchon2022.fr${encodedLink}`;

  return (
    <StyledShareBlock>
      <div>Partagez la rvolution fiscale</div>
      <div style={{ textAlign: "center" }}>
        <ShareLink
          href={`https://www.facebook.com/sharer/sharer.php?u=${URL}${encodedLink}`}
          bg="#1778F2"
        >
          Je partage sur Facebook
        </ShareLink>
        <ShareLink href={urlTwitter} bg="#4099FF">
          Je partage sur Twitter
        </ShareLink>
        <ShareLink
          target="_blank"
          href="https://melenchon2022.fr/"
          bg="#F53B3B"
        >
          Je soutiens Jean-Luc Mlenchon
        </ShareLink>
      </div>
    </StyledShareBlock>
  );
};

